import {Component, ComponentDidLoad, h, Method, Prop, Watch} from '@stencil/core';

@Component({
  tag: 'soul-prefetch',
  styleUrl: 'soul-prefetch.scss',
  shadow: true,
})
export class SoulPrefetch implements ComponentDidLoad {

  @Prop({reflect: true}) public fetchOnLoad: boolean = false;
  @Prop({reflect: false}) public components: string[];
  @Prop({reflect: true}) public delay: 0;

  private container: HTMLElement;

  render() {
    return <div class="soul-prefetch" ref={el => this.container = el as HTMLDivElement}></div>;
  }

  @Watch('components')
  async componentDidLoad(): Promise<void> {
    if (this.fetchOnLoad) {
      this.fetchComponents();
    }
  }

  @Method()
  async fetchComponents(): Promise<void> {
    setTimeout(() => {
      const htmlElementsToRemove: HTMLElement[] = [];
      this.components.forEach((component) => {
        const htmlElement = document.createElement(component);
        htmlElementsToRemove.push(htmlElement);
        this.container.appendChild(htmlElement);
      });
      setTimeout(() => {
        htmlElementsToRemove.forEach(el => el.remove());
      }, 50000)
    }, this.delay);
  }
}
